.news-intro-col {
    display: -ms-inline-flexbox;
    display: inline-flex;
    color: black;
    height: 100%;
  }
  
  .news-intro-panel {
    margin: auto 5px;
    text-align: left;
  }
  
  .news-description {
    text-align: justify;
    font-size: 18px;
  }
  
  .news-chip {
    font-size: 18px;
  }
  
  .fill {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      overflow: hidden;
  }
  
  .fill img {
      padding-left: 20px;
      padding-right: 20px;
      -ms-flex-negative: 0;
          flex-shrink: 0;
      min-width: 100%;
      max-height: 250px;
      -o-object-fit: cover;
         object-fit: cover;
  }
  
  .label{
    margin-right: 10px;
  }


